// 1. vuevue-routervueusevue-router
import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);
import Home from "../views/Home.vue"
import Login from "../views/Login.vue"
import Register from "../views/Register.vue"
import Right from "../components/Right.vue"
import MP4 from "../components/mp4.vue"
import ShareFile from "../components/ShareFile.vue"
import ShareFileCode from "../views/ShareFileCode.vue"
// 2. vue-routervue-routermain.js
export default new Router({
  // history#
  base: '/vue/',
  // mode: "history",
  // assetsPublicPath: './',
  routes:[
    // 
    {
      name:"Home",
      path: "/home",
      component: Home,
    },
	{
	  name:"MP4",
	  path: "/MP4",
	  component: MP4,
	},
	{
	  name:"ShareFileCode",
	  path: "/shareFileCode/:checkcode",
	  component: ShareFileCode
	},
	{
	  name:"ShareFile",
	  path: "/shareFile",
	  component: ShareFile
	},
	{
	  name:"Login",
	  path: "/",
	  component: Login,
	  alias:"/login"
	},
	{
	  name:"Register",
	  path: "/register",
	  component: Register
	},
	
  ]
})